@import "../../global.scss";
.works {
  background-color: $secColor;

  height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  .tag {
    width: 100px;
    height: 20px;
    border-radius: 2rem;
    padding: 2px 10px;
    font-size: 12px;
    margin-right: 5px;

    outline: 2px solid;
  }
  .java {
    background-color: #fff9c4;
  }
  .swing {
    background-color: #ffcdd2;
  }
  .javaFx {
    background-color: #fff3e0;
  }
  .reactJs {
    background-color: #b3e5fc;
  }
  .cs {
    background-color: #c5cae9;
  }
  .python {
    background-color: #b9f6ca;
  }
  .firebase {
    background-color: #ffecb3;
  }
  .jupyter {
    background-color: #ffe0b2;
  }

  .cardContainer {
    align-self: center;

    width: 950px;
    height: 88%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-evenly;
    align-content: space-around;

    margin-left: 50px;
    @include mobile {
      flex-direction: column;
      flex-wrap: nowrap;
      margin-left: 0px;
      width: 90%;
    }
    .card {
      @include mobile {
        width: 90%;
        margin: 5px 20px;
      }
      flex: 1 1 1;
      margin: 25px 30px;
      background-color: #f5f5f5;
      border-radius: 0.5rem;
      padding-bottom: 1.1rem;
      transition: transform 0.25s ease-in-out;
      &:hover {
        transform: scale3d(1.05, 1.05, 1);
      }
    }
  }
}
