@import "./global.scss";
.app{
height: 100vh;
background-color: $secColor;

    .sections{
  
    width: 100vw;
    height: calc(100vh - 70px);
    
    position: relative;
    top: 70px;
    scroll-behavior: smooth;
    scroll-snap-type: y mandatory;
    scrollbar-width: none;//for firefox
       &::-webkit-scrollbar{
           display: none;
       }
    
    >*{
        // background-color: $mainColor;
            width: 100vw;
            height: calc(100vh - 70px);
        scroll-snap-align: start;
        }
    // }
    //    
    }
}